"use client";
import { useSession, signIn, signOut } from "next-auth/react";
import React, { useState, FormEvent, ChangeEvent } from 'react';
import { ShieldCheck, LogIn, Chrome } from 'lucide-react'; // Added icons

const Login: React.FC = () => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });

  const { data: session, status } = useSession();

  function handleChange(e: ChangeEvent<HTMLInputElement>) {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  }

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // Usually you would use signIn('credentials', formData) here
    console.log('Authenticating:', formData);
  };

  // 1. LOADING STATE
  if (status === "loading") {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen bg-[#121212]">
        <span className="loading loading-spinner loading-lg text-amber-500"></span>
        <p className="mt-4 text-gray-400 animate-pulse font-mono">Verifying credentials...</p>
      </div>
    );
  }

  // 2. LOGGED IN STATE (Management Style)
  if (session) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-[#121212] px-6">
        <div className="bg-white/5 border border-white/10 rounded-2xl p-10 text-center shadow-2xl max-w-sm w-full backdrop-blur-md">
          <div className="flex justify-center mb-4">
            <div className="bg-amber-600/20 p-4 rounded-full">
              <ShieldCheck className="text-amber-500" size={48} />
            </div>
          </div>
          <h2 className="text-2xl font-bold text-white mb-2">Authenticated</h2>
          <p className="text-gray-400 mb-8 italic">
            Session active for: <br />
            <span className="text-amber-500 font-semibold not-italic">{session.user?.email}</span>
          </p>
          <div className="flex flex-col gap-3">
            <a href="/admin/dashboard" className="btn bg-amber-600 hover:bg-amber-500 text-white border-none">
              Enter Dashboard
            </a>
            <button className="btn btn-ghost text-gray-400 hover:text-red-400" onClick={() => signOut()}>
              End Session
            </button>
          </div>
        </div>
      </div>
    );
  }

  // 3. LOGIN FORM
  return (
    <div className="flex min-h-screen flex-col justify-center bg-[#121212] px-6 py-12 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-sm">
        <div className="flex justify-center mb-6">
          <LogIn className="text-amber-500" size={40} />
        </div>
        <h2 className="text-center text-3xl font-bold tracking-tight text-white font-serif">
          Management Portal
        </h2>
        <p className="mt-2 text-center text-gray-500 text-sm">
          Authorized personnel only.
        </p>
      </div>

      <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label htmlFor="email" className="block text-sm font-medium text-gray-300">
              Identity (Email)
            </label>
            <div className="mt-2">
              <input
                id="email"
                name="email"
                type="email"
                required
                value={formData.email}
                onChange={handleChange}
                className="block w-full rounded-md border-0 bg-white/5 py-2.5 px-3 text-white shadow-sm ring-1 ring-inset ring-white/10 focus:ring-2 focus:ring-inset focus:ring-amber-500 sm:text-sm"
                placeholder="admin@system.com"
              />
            </div>
          </div>

          <div>
            <div className="flex items-center justify-between">
              <label htmlFor="password" className="block text-sm font-medium text-gray-300">
                Access Key
              </label>
              <div className="text-sm">
                <a href="#" className="font-semibold text-amber-500 hover:text-amber-400">
                  Reset Key?
                </a>
              </div>
            </div>
            <div className="mt-2">
              <input
                id="password"
                name="password"
                type="password"
                required
                value={formData.password}
                onChange={handleChange}
                className="block w-full rounded-md border-0 bg-white/5 py-2.5 px-3 text-white shadow-sm ring-1 ring-inset ring-white/10 focus:ring-2 focus:ring-inset focus:ring-amber-500 sm:text-sm"
              />
            </div>
          </div>

          <div className="space-y-4">
            <button
              type="submit"
              className="flex w-full justify-center rounded-md bg-amber-600 px-3 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-amber-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-amber-500 transition-all"
            >
              Secure Login
            </button>

            <div className="relative">
              <div className="absolute inset-0 flex items-center"><span className="w-full border-t border-white/10"></span></div>
              <div className="relative flex justify-center text-xs uppercase"><span className="bg-[#121212] px-2 text-gray-500">Federated Auth</span></div>
            </div>

            <button
              type="button"
              className="flex w-full items-center justify-center gap-3 rounded-md bg-white text-black px-3 py-2.5 text-sm font-semibold shadow-sm hover:bg-gray-200 transition-all"
              onClick={() => signIn("google")}
            >
              <Chrome size={18} />
              Continue with Google
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;